.hero{
  background-color: rgb(113, 176, 200);
  color: white;
  width: 100%;
}

.hero-section{
  background-image: url(../assets/images/hero_image.jpg);
  background-repeat: no-repeat;
  background-size: 100%;
  background-position: cover;
  width: 100%;
  height: 100vh;
  padding: 0px 90px;
  box-sizing: border-box;
}

.hero-text{
  padding: 190px 0px 90px 0px;
}

.hero-text h2{
  letter-spacing: 2px;
  color: rgb(7, 7, 77);
}

.hero-text h2 span{
  font-size: 60px;
  color: white;
  
}

.hero-text p{
  font-size: 19px;
  padding-top: 20px;
  font-weight: bold;
}

@media screen and (max-width: 450px){
  .hero-text{
    padding: 10px 0px 40px 0px;
  }

  .hero-text h2{
    display: none;
  }
  .hero-text p{
    padding-top: 0px;
  }

  .hero-section{
    width: 100%;
    height: 250%;
    padding: 0px 20px;
  }
}

@media screen and (min-width: 451px) and (max-width: 912px){

  .hero{
    padding: 0px 0px 0px 0px;
  }

  .hero-section{
    width: 100%;
    padding: 0px 20px;
    height: 200vh;
  }

  .hero-text{
    padding: 0px 0px 10px 0px;
    margin-top: -90px;
  }
  .hero-text h2 span{
    font-size: 50px;    
  }
}

@media screen and (min-width: 913px) and (max-width: 1028px){

  .hero{
    padding: 0px 40px;
  }

  .hero-section{
    width: 100%;
    height: 5020%;
    padding: 0px 20px;
  }

  .hero-text{
    padding: 0px 0px 90px 0px;
    margin-top: -30px;
  }
}

@media screen and (max-width: 1280px){

  .hero{
    padding: 0px 0px 0px 0px;
  }

  .hero-section{
    width: 100%;
    box-sizing: border-box;
    height: 65vh;
    padding: 0px 20px;
  }

  .hero-text{
    padding: 90px 0px 90px 0px;
  }
}