.searchBox{
  margin: 0px 0px;
}

.search{
  width: 320px;
  height: 50px;
  font-size: 18px;
  padding: 10px;
}

.search .input{
  border-top-left-radius: 10px;
  border-bottom-left-radius: 10px;
  border-right: 0px;
}

.search .select{
  border-top-right-radius: 10px;
  border-bottom-right-radius: 10px;
}

.search:focus{
  outline: none;
}

.search_btn{
  width: 100px !important;
  background-color: rgb(7, 7, 77);
  font-size: 16px;
  /* padding-top: 3px; */
  font-weight: bold;
  color: #fff;
  cursor: pointer;
}

.search_btn:hover{
  background-color: #fff;
  color: rgb(7, 7, 77);
}

@media screen and (max-width: 355px){
  .searchBox{
    margin-top: -25px;
  }

  .search{
    width: 250px;
    margin-top: 10px;
  }
}

@media screen and (min-width: 451px) and (max-width: 606px){
  .searchBox{
    margin-top: -45px;
  }

  .search{
    width: 250px;
    margin-top: 10px;
  }
} 

@media screen and (min-width: 913px) and (max-width: 1280px) {
  .searchBox{
    margin-top: -75px;
  }

  .search{
    width: 250px;
    margin-top: 10px;
  }
} 